import kMyTeam from "@/components/k-ui/k-team/k-myTeam.vue"
export default {
	components:{
		kMyTeam
	},
	data() {
		return {
			currUserId: '',
			datas: []
		};
	},
	onLoad(options) {
		uni.showLoading({
			title: '正在加载'
		})
		this.currUserId = options.id
		this.loadData()
	},
	methods:{
		getTeamDetail(id){
			console.log(id)
			uni.navigateTo({
				url: '/pages/team/my-team-detail?id=' + id
			})
		},
		async loadData(){
			const res = await this.$api.request({
				url: '/team/myTeam',
				data: {
					id: this.currUserId
				}
			})
			console.log(res)
			this.datas = res.data
			uni.hideLoading()
		},
	}
}
